<template>
  <div id="app">
    <h1>EXIF Info</h1>
    <span style="color: #bbb">ILCE-6000 @ 24mm  /11  1/320s  ISO 100</span>
    <br>
    <br>
    <picture-input
      ref="pictureInput"
      @change="onChange"
      width="400"
      height="400"
      margin="16"
      accept="image/jpeg"
      :hideChangeButton="true"
      :removable="false"
      :customStrings="{
        upload: '<h1>Bummer!</h1>',
        drag: '<svg xmlns=\'http://www.w3.org/2000/svg\' style=\'fill: #D7D7D7\' height=\'200\' viewBox=\'0 0 512 512\'><path d=\'M464 64H48C21.49 64 0 85.49 0 112v288c0 26.51 21.49 48 48 48h416c26.51 0 48-21.49 48-48V112c0-26.51-21.49-48-48-48zm-6 336H54a6 6 0 0 1-6-6V118a6 6 0 0 1 6-6h404a6 6 0 0 1 6 6v276a6 6 0 0 1-6 6zM128 152c-22.091 0-40 17.909-40 40s17.909 40 40 40 40-17.909 40-40-17.909-40-40-40zM96 352h320v-80l-87.515-87.515c-4.686-4.686-12.284-4.686-16.971 0L192 304l-39.515-39.515c-4.686-4.686-12.284-4.686-16.971 0L96 304v48z\'/></svg>'
      }">
    </picture-input>

    <div v-if="exifstr" style="margin-top: 25px">
      <h4>{{ exifstr }}</h4>

      <div>
        <button type="button" v-clipboard:copy="message">
          <svg xmlns="http://www.w3.org/2000/svg" height="16" viewBox="0 0 384 512"><path d="M336 64h-80c0-35.29-28.71-64-64-64s-64 28.71-64 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h288c26.51 0 48-21.49 48-48V112c0-26.51-21.49-48-48-48zm-6 400H54a6 6 0 0 1-6-6V118a6 6 0 0 1 6-6h42v36c0 6.627 5.373 12 12 12h168c6.627 0 12-5.373 12-12v-36h42a6 6 0 0 1 6 6v340a6 6 0 0 1-6 6zM192 40c13.255 0 24 10.745 24 24s-10.745 24-24 24-24-10.745-24-24 10.745-24 24-24"/></svg>
        </button>
        <button type="button" @click="editmode = !editmode">
          <svg xmlns="http://www.w3.org/2000/svg" height="16" viewBox="0 0 576 512"><path d="M402.3 344.9l32-32c5-5 13.7-1.5 13.7 5.7V464c0 26.5-21.5 48-48 48H48c-26.5 0-48-21.5-48-48V112c0-26.5 21.5-48 48-48h273.5c7.1 0 10.7 8.6 5.7 13.7l-32 32c-1.5 1.5-3.5 2.3-5.7 2.3H48v352h352V350.5c0-2.1.8-4.1 2.3-5.6zm156.6-201.8L296.3 405.7l-90.4 10c-26.2 2.9-48.5-19.2-45.6-45.6l10-90.4L432.9 17.1c22.9-22.9 59.9-22.9 82.7 0l43.2 43.2c22.9 22.9 22.9 60 .1 82.8zM460.1 174L402 115.9 216.2 301.8l-7.3 65.3 65.3-7.3L460.1 174zm64.8-79.7l-43.2-43.2c-4.1-4.1-10.8-4.1-14.8 0L436 82l58.1 58.1 30.9-30.9c4-4.2 4-10.8-.1-14.9z"/></svg>
        </button>
      </div>
    </div>


    <div v-if="editmode">
      <input type="text" v-model="exif.model" />
      <input type="text" v-model="exif.focalLength" />
      <input type="text" v-model="exif.fNumber" />
      <input type="text" v-model="exif.exposureTime" />
      <input type="text" v-model="exif.isoSpeed" />
    </div>
  </div>
</template>

<script>
import PictureInput from 'vue-picture-input'
import exif from 'exif-js'

export default {
  name: 'app',
  data () {
    return {
      editmode: false,
      exifdata: null,
      message: '',
      exif: {
        model: '',
        focalLength: '',
        fNumber: '',
        exposureTime: '',
        isoSpeed: ''
      }
    }
  },
  computed: {
    exifstr() {
      // Sony Alpha a6000 with Sony Sonnar T* E 24mm f/1.8 ZA
      // 24mm  /4.5  1/125s  ISO 100

      if (this.exifdata)
      {
        const e = this.exif

        this.message = `${e.model} @ ${e.focalLength}mm  /${e.fNumber}  ${e.exposureTime}s  ISO ${e.isoSpeed}`

        return this.message
      }
      return
    }
  },
  components: {
    PictureInput
  },
  methods: {
    onChange () {
      var that = this

      if (this.$refs.pictureInput.image) {


            var file = this.$refs.pictureInput.file
            if (file && file.name) {
                exif.getData(file, function() {
                  // console.log('exifdata', this.exifdata)
                  // console.log('iptcdata', this.iptcdata)

                  if (this.exifdata) {
                      that.exifdata = this.exifdata

                      that.exif.model = this.exifdata.Model
                      that.exif.focalLength = this.exifdata.FocalLength
                      that.exif.fNumber = this.exifdata.FNumber
                      that.exif.exposureTime = `1/${1/this.exifdata.ExposureTime}`
                      that.exif.isoSpeed = this.exifdata.ISOSpeedRatings
                  } else {
                      that.exifdata = "No EXIF data found in image '" + file.name + "'."
                  }
                });
            }


        console.log('Picture loaded.')
      } else {
        console.log('FileReader API not supported: use the <form>, Luke!')
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

button {
  margin: 10px 3px;
  border: 0;
  background: 0;
  outline: none;
  background: #efefef;
  width: 32px;
  height: 32px;
  text-align: center;
  padding: 0;
  border-radius: 100%;
}

button:focus,
button:hover {
  background: #dfdfdf;
}

button svg {
 fill: #444;
 display: block;
 margin: 0 auto;
}
</style>



